import { Point2D } from "./KanjiVGTypes";

export class Vector {

  constructor(public x: number, public y: number, public location: Point2D = { x: 0, y: 0 }) {
  }
  static fromPoints(start: Point2D, end: Point2D) {
    return new Vector(
      end.x - start.x,
      end.y - start.y,
      start
    );
  }
  /**
   * 
   * @param v
   */
  add(v: Vector): Vector {
    return new Vector(this.x + v.x, this.y + v.y);
  }
  /**
   * 
   * @param x
   * @param y
   */
  multi(x: number, y?: number): Vector {   // 
    y = y || x;
    return new Vector(this.x * x, this.y * y);
  }
  /**
   * 
   * @param v
   */
  dot(v: Vector): number {
    return this.x * v.x + this.y * v.y;
  }

  /**
   * 
   * @param v
   */
  cross(v: Vector): number {    // 
    return this.x * v.y - v.x * this.y;
  }

  /**
   * 
   */
  get length(): number {
    return Math.sqrt(Math.pow(this.x, 2) + Math.pow(this.y, 2));
  }

  /**
   * 
   * @param v
   */
  cosineSimilarity(v: Vector): number {
    // https://code.i-harness.com/ja/q/7f031
    // https://gist.github.com/robertknight/5410420
    return this.dot(v) / (this.length * v.length);
  }
  toPath(): string {
    if (this.location) {
      let path = "M" + this.location.x + "," + this.location.y;
      path += "l" + this.x + "," + this.y;
      return path;
    }
    return "";
  }
}



